#home {
  display: flex;
  position: relative;
  background-color: var(--your-background-color);

  .left-bg {
    flex: 1;
    // background: url("../../assets/software_gif.gif");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    min-height: 70vh;

    @media screen and (max-width: 1024px) {
      display: none;
    }
  }

  .right-bg {
    flex: 1;
    background-color: var(--your-background-color);
  }

  // Maintain the wrapper styles from the uncommented code
  .app__wrapper {
    padding: 0;

    .copyright {
      display: none;
    }
  }
}

.app__header {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 12rem 2rem 0;

  @media screen and (min-width: 2000px) {
    padding-top: 14rem;
  }

  @media screen and (max-width: 450px) {
    flex-direction: column;
    padding: 8rem 1rem 2rem;
  }

  .app__header-info {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 2rem;

    @media screen and (max-width: 1200px) {
      width: 100%;
    }
  }
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .badge-cmp,
  .tag-cmp {
    padding: 1rem 2rem;
    background: var(--white-color);
    border-radius: 15px;
    width: auto;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  }

  .tag-cmp {
    flex-direction: column;
    margin-top: 1.5rem;

    p {
      width: 100%;
      text-transform: uppercase;
      text-align: center;
    }
  }

  span {
    font-size: 2.5rem;

    @media screen and (min-width: 2000px) {
      font-size: 5rem;
    }
  }
}

.app__header-circles {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  margin-top: 2rem;
  flex-wrap: wrap;

  div {
    border-radius: 50%;
    background: var(--white-color);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    
    img {
      width: 60%;
      height: 60%;
    }
  }

  div:nth-child(1) {
    width: 80px;
    height: 80px;
  }

  div:nth-child(2) {
    width: 120px;
    height: 120px;
  }

  div:nth-child(3) {
    width: 150px;
    height: 150px;
  }

  div:nth-child(4) {
    width: 120px;
    height: 120px;
  }

  div:nth-child(5) {
    width: 80px;
    height: 80px;
  }

  @media screen and (min-width: 2000px) {
    div:nth-child(1) {
      width: 160px;
      height: 160px;
    }

    div:nth-child(2) {
      width: 200px;
      height: 200px;
    }

    div:nth-child(3) {
      width: 250px;
      height: 250px;
    }

    div:nth-child(4) {
      width: 200px;
      height: 200px;
    }

    div:nth-child(5) {
      width: 160px;
      height: 160px;
    }
  }

  @media screen and (max-width: 1200px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 1rem;

    div {
      margin: 1rem;
    }
  }
}

.app__header-img {
  flex: 1;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative;

  img {
    width: 100%;
    object-fit: contain;
    z-index: 1;
  }

  .overlay_circle {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 90%;
  }

  @media screen and (max-width: 1200px) {
    margin: 2rem 0;
  }
}





















