.app__resume-section {
  display: flex;
  justify-content: center; /* Center content horizontally */
  align-items: center; /* Center content vertically */
  padding: 3rem;
  height: 90vh; /* 90% of the viewport height */
  background-color: var(--white-color); /* Optional background color */
  border-radius: 15px; /* Rounded corners */
}

.app__resume-content {
  display: flex;
  height: 100%; /* Full height of the parent section */
  width: 120%; /* Set a maximum width for the content */
  max-width: 2000px; /* Maximum width for larger screens */
  background-color: var(--white-color); /* Match background color to the section */
  border-radius: 15px; /* Rounded corners */
  overflow: hidden; /* Prevent overflow from child elements */

  .app__resume-image {
    flex: 1 1 70%; /* Allow the image to take up 70% of the container */
    display: flex; /* Use flexbox for vertical alignment */
    justify-content: center; /* Center horizontally */
    align-items: center; /* Center vertically */
    height: 100%; /* Ensure it takes full height of the parent */

    img {
      max-width: 100%; /* Full width of the container */
      max-height: 100%; /* Maintain aspect ratio within the container */
      height: auto; /* Automatically adjust height based on width */
      border-radius: 15px 0 0 15px; /* Rounded corners on the left side */
    }
  }

  .app__resume-actions {
    flex: 1 1 60%; /* Allow the actions to take up 60% of the container */
    padding: 2rem; /* Increased padding inside the action section */

    h2 {
      font-size: 2.5rem; /* Increased title font size */
      color: #708090; /* Change title color to grey */
      margin-top: 7rem; /* Add space above the title */
      margin-bottom: 1.5rem; /* Space below the title */
      text-align: left; /* Align title to the left */
    }

    .app__resume-icons {
      display: flex; /* Flex layout for icons */
      margin-top: 2rem; /* Adjusted space above icons */
      gap: 3rem; /* Space between icons */

      .icon-button {
        display: flex; /* Flex for centering icon */
        align-items: center; /* Center icon vertically */
        justify-content: center; /* Center icon horizontally */
        width: 60px; /* Increased width for buttons */
        height: 60px; /* Increased height for buttons */
        background-color: var(--primary-color); /* Background color */
        color: #4682B4; /* Icon color */
        border-radius: 50%; /* Round buttons */
        transition: background-color 0.3s ease; /* Transition for hover effect */

        &:hover {
          background-color: #4682B4; /* Change to another color on hover */
          color: var(--primary-color);
        }

        svg {
          width: 25px; /* Increased icon size */
          height: 25px; /* Increased icon size */
        }
      }
    }
  }
}




/* Responsive adjustments for smaller screens */
@media (max-width: 1300px) {
  .app__resume-content {
    flex-direction: column; /* Stack items vertically */
    align-items: center; /* Center align content */
    width: 100%; /* Full width for small screens */
    padding: 1rem; /* Add some padding */

    .app__resume-image {
      width: 100%; /* Full width for the image */
      margin-bottom: 1rem; /* Space below the image */
      display: flex;
      justify-content: center;

      img {
        width: 80%; /* Set image width */
        max-width: 100%;
        border-radius: 15px; /* Uniform rounded corners */
      }
    }

    .app__resume-actions {
      text-align: center; /* Center text for small screens */

      h2 {
        font-size: 1.8rem; /* Slightly smaller font size */
        margin-top: 1rem; /* Add space above heading */
        margin-bottom: 1rem; /* Space below heading */
      }

      .app__resume-icons {
        display: flex; /* Flex layout for icons */
        justify-content: center; /* Center icons */
        gap: 1rem; /* Space between icons */

        .icon-button {
          width: 50px; /* Smaller buttons */
          height: 50px;
        }
      }
    }
  }
}











